import { makeStyles } from '@material-ui/core/styles'
import HomeIcon from '@material-ui/icons/Home'
import MailIcon from '@material-ui/icons/Mail'
import MenuIcon from '@material-ui/icons/Menu'
import FaceIcon from '@material-ui/icons/Face'
import NextWeekIcon from '@material-ui/icons/NextWeek'
import SportsHandballIcon from '@material-ui/icons/SportsHandball'
import SpeedDial from '@material-ui/lab/SpeedDial'
import SpeedDialAction from '@material-ui/lab/SpeedDialAction'
import React from 'react'
import { useHistory } from 'react-router-dom'

const actions = [
  { icon: <HomeIcon style={{ color: '#1c1c25' }} />, name: 'HOME' },
  { icon: <FaceIcon style={{ color: '#1c1c25' }} />, name: 'ABOUT ME' },
  { icon: <SportsHandballIcon style={{ color: '#1c1c25' }} />, name: 'ACHIEVEMENT' },
  { icon: <NextWeekIcon style={{ color: '#1c1c25' }} />, name: 'STACKS' },
  { icon: <MailIcon style={{ color: '#1c1c25' }} />, name: 'CONTACT' },
]

export default function SpeedDialTooltipOpen() {
  const [open, setOpen] = React.useState(false)
  let history = useHistory()

  const handleOpen = () => {
    setOpen(true)
  }

  const handleClose = (name) => {
    switch (name) {
      case 'HOME':
        history.push('/')
        break

      case 'ABOUT ME':
        history.push('/about')
        break

      case 'STACKS':
        history.push('/stacks')
        break

      default:
        break
    }
    setOpen(false)
  }

  return (
    <SpeedDial
      ariaLabel="SpeedDial tooltip example"
      hidden={false}
      icon={<MenuIcon />}
      onClose={handleClose}
      onOpen={handleOpen}
      open={open}
      direction={'down'}
      FabProps={{ style: { background: 'none' } }}
    >
      {actions.map((action) => (
        <SpeedDialAction
          key={action.name}
          name={action.name}
          icon={action.icon}
          tooltipTitle={<span>{action.name}</span>}
          tooltipOpen={false}
          onClick={() => {
            handleClose(action.name)
            // console.log(e.currentTarget.name)
          }}
          tooltipPlacement={'right'}
        />
      ))}
    </SpeedDial>
  )
}
