import React from 'react'
import anime from 'animejs'

export const StarrySky = () => {
  const stats = {
    num: 60,
    vw: Math.max(document.documentElement.clientWidth, window.innerWidth || 0),
    vh: Math.max(document.documentElement.clientHeight, window.innerHeight || 0),
  }

  const starryNight = () => {
    anime({
      targets: ['#sky .star'],
      opacity: [
        {
          duration: 700,
          value: '0',
        },
        {
          duration: 700,
          value: '1',
        },
      ],
      easing: 'linear',
      loop: true,
      delay: (el, i) => 50 * i,
    })
  }

  const shootingStars = () => {
    anime({
      targets: ['#shootingstars .wish'],
      easing: 'linear',
      loop: true,
      delay: (el, i) => 5000 * i,
      opacity: [
        {
          duration: 700,
          value: '0.1',
          value: '1',
        },
      ],
      width: [
        {
          value: '150px',
        },
        {
          value: '0px',
        },
      ],
      translateX: 350,
    })
  }

  const randomRadius = () => {
    return Math.random() * 0.7 + 0.6
  }

  const getRandomX = () => {
    return Math.floor(Math.random() * Math.floor(stats.vw)).toString()
  }

  const getRandomY = () => {
    return Math.floor(Math.random() * Math.floor(stats.vh)).toString()
  }

  const moveSky = () => {
    const move = '100vh'
    const long = 31000

    anime({
      targets: '#sky',
      translateY: move,
      duration: long,
      loop: true,
      easing: 'linear',
    })

    anime({
      targets: '#sky2',
      translateY: move,
      duration: long,
      loop: true,
      easing: 'linear',
    })
  }

  React.useEffect(() => {
    starryNight()
    shootingStars()
    moveSky()
  }, [])

  const SkyObject = () => {
    var randomStarPoints = []
    for (let index = 0; index < 60; index++) {
      const cx = getRandomX()
      const cy = getRandomY()
      const r = randomRadius()
      randomStarPoints.push({ cx: cx, cy: cy, r: r })
    }

    return (
      <>
        <svg id="sky2">
          {randomStarPoints.map((x, y) => (
            <circle
              cx={x.cx}
              cy={x.cy}
              r={x.r}
              stroke="none"
              strokeWidth="0"
              fill="white"
              key={y}
              className="star"
            />
          ))}
        </svg>
        <svg id="sky">
          {randomStarPoints.map((x, y) => (
            <circle
              cx={x.cx}
              cy={x.cy}
              r={x.r}
              stroke="none"
              strokeWidth="0"
              fill="white"
              key={y}
              className="star"
            />
          ))}
        </svg>
      </>
    )
  }

  return (
    <div>
      {/* Credits // Twinkling Night Sky by Sharna, edited by Chatbordin. */}
      <SkyObject />
      <div id="shootingstars">
        {[...Array(60)].map((x, y) => (
          <div
            key={y}
            className="wish"
            style={{
              left: `${getRandomY()}px`,
              top: `${getRandomX()}px`,
            }}
          />
        ))}
      </div>
    </div>
  )
}
