import './App.css'
import React from 'react'
import { Background } from './components/assets/StyledBackground'
import { BrowserRouter, Switch, Route } from 'react-router-dom'
import StyledSpeedDial from './components/assets/StyledSpeedDial'
import { Grid } from '@material-ui/core'
import { StarrySky } from './components/assets/StarrySky'
import { StyledRocketShip } from './components/assets/StyledRocketShip'
import { TransparentCard } from './components/assets/TransparentCard'
import { Stacks } from './components/assets/Stacks'

function App() {
  return (
    <BrowserRouter>
      <Switch>
        <Grid id="rootBG" container direction="column" justify="center" alignItems="center">
          <Background />
          <Grid item style={{ position: 'absolute', top: '0', left: '0', alignSelf: 'flex-start' }}>
            <StarrySky />
          </Grid>
          <Grid item style={{ position: 'absolute', left: '16px', top: '16px', zIndex: '1' }}>
            <StyledSpeedDial />
          </Grid>
          <Grid item style={{ position: 'absolute' }}>
            <Route exact path="/" component={StyledRocketShip} />
            <Route exact path="/about" component={TransparentCard} />
          </Grid>
          <Grid item style={{ position: 'absolute', top: '16px' }}>
            <Route exact path="/stacks" component={Stacks} />
          </Grid>
        </Grid>
      </Switch>
    </BrowserRouter>
  )
}

export default App
