import React from 'react'
import { styled } from '@material-ui/core/styles'
import anime from 'animejs'
import Typography from '@material-ui/core/Typography'

const frameSize = { width: '120', height: '150' }

const moveShip = () => {
  anime({
    targets: ['#shipFrame'],
    translateY: 260,
    duration: 2900,
    easing: 'easeInCubic',
    direction: 'alternate',
    loop: true,
  })
}

const turbulenceFlickering = () => {
  anime({
    targets: ['#turbulence'],
    opacity: 0.1,
    duration: 2900,
    easing: 'easeInCubic',
    direction: 'alternate',
    loop: true,
  })
}

