const ShipBody = styled(({ color, ...other }) => (
  <svg
    width={frameSize.width}
    height={frameSize.height}
    viewBox="0 0 1114 1370"
    // fill="none"
    xmlns="http://www.w3.org/2000/svg"
    style={{ filter: 'drop-shadow(-10px 15px 10px #4444dd);' }}
    {...other}
  >
    <path
      d="M463.851 64.532C496.979 -20.3002 617.021 -20.3003 650.149 64.5319L1106.5 1233.12C1132.1 1298.7 1083.74 1369.5 1013.35 1369.5H100.652C30.2556 1369.5 -18.1046 1298.7 7.5026 1233.12L463.851 64.532Z"
      fill="rgba(255,255,255,0.1)"
    />
  </svg>
))({})

const ShipFrame = styled(({ color, ...other }) => <div id="shipFrame" {...other} />)({
  marginTop: '0%',
  // backdropFilter: 'blur(10px)',
  // position: 'absolute',
  // top: '0',
  // left: '0',
  width: `${frameSize.width}px`,
  height: `${frameSize.height}px`,
  // boxShadow: '0 15px 35px rgba(0,0,0,0.5)',
  // borderRadius: '15px',
  // background: 'rgba(255,255,255,0.5)',
  transformStyle: 'preserve-3d',
  transition: '1s',
  // transform: 'rotateY(180deg)',
  // backfaceVisibility: 'hidden',
  // '&:hover': {
  //   transform: 'rotateY(180deg)',
  // },
  '&::before': {
    content: '""',
    position: 'absolute',
    top: '43px',
    left: '41px',
    width: '39px',
    height: '39px',
    background: '#fff',
    borderRadius: '50%',
    opacity: '0.5',
  },

  '&::after': {
    content: '""',
    position: 'absolute',
    top: '144px',
    left: '26px',
    width: '66px',
    height: '10px',
    background: '#fff',
    borderRadius: '16%',
    opacity: '0.5',
  },
})

const ShipTurbulence = styled(({ color, ...other }) => <div id="turbulence" {...other} />)({
  position: 'absolute',
  left: '55px',

  // width: '200px',
  // height: '200px',
  '&::before': {
    content: '""',
    position: 'absolute',
    // top: '160px',
    // left: '50px',
    // transform: translateX(-50%),
    width: '10px',
    height: '50px',
    background: 'linear-gradient(#00d0ff,transparent)',
    // filter: 'blur(20px)',
  },
  '&::after': {
    content: '""',
    position: 'absolute',
    // top: '160px',
    // left: '50px',
    // transform: translateX(-50%),
    width: '10px',
    height: '109px',
    background: 'linear-gradient(#00d0ff,transparent)',
    filter: 'blur(20px)',
  },
})

export const StyledRocketShip = () => {
  React.useEffect(() => {
    moveShip()
    turbulenceFlickering()
  }, [])
  return (
    <ShipFrame>
      <ShipBody />
      <div
        style={{ position: 'absolute', top: '83px', left: '76px', width: '39px', height: '39px' }}
      >
        <Typography
          style={{ fontFamily: 'Kanit', color: '#fff', fontSize: '6px', textAlign: 'center' }}
        >
          I am a CSS master.
        </Typography>
      </div>
      <div style={{ position: 'absolute', top: '90px', left: '40px' }}>
        <ShipBoom />
      </div>
      <ShipTurbulence />
    </ShipFrame>
  )
}
