import { makeStyles } from '@material-ui/core/styles'
import { Card, Avatar } from '@material-ui/core/'
import Typography from '@material-ui/core/Typography'
import Grid from '@material-ui/core/Grid'
import reactSVG from './svg/react.svg'
import firebaseSVG from './svg/firebase.svg'
import nodejsSVG from './svg/nodejs.svg'
import nginxSVG from './svg/nginx.svg'
import postgresqlSVG from './svg/postgresql.svg'
import pythonSVG from './svg/python.svg'

const useStyles = makeStyles({
  root: {
    width: 'fit-content',
    height: 'fit-content',
    boxShadow: '0 15px 35px rgba(0,0,0,0.5)',
    borderRadius: '15px',
    background: 'rgba(255,255,255,0.05)',
    backdropFilter: 'blur(10px)',
    transformStyle: 'preserve-3d',
    transition: '1s',
    color: '#fff',
    paddingRight: '6px',
  },
  media: {
    height: '10px',
  },
  small: {
    width: '80%',
    height: '80%',
  },
})

const stacks = [
  { name: 'React', icon: reactSVG },
  { name: 'Firebase', icon: firebaseSVG },
  { name: 'NodeJs', icon: nodejsSVG },
  { name: 'Express', icon: '/' },
  { name: 'NginX', icon: nginxSVG },
  { name: 'PostgreSQL', icon: postgresqlSVG },
  { name: 'Python', icon: pythonSVG },
]

export function Stacks() {
  const classes = useStyles()

  return (
    <Grid
      container
      spacing={1}
      direction="column"
      justify="center"
      style={{ marginLeft: '0px', marginRight: '0px', height: '100vh' }}
    >
      {stacks.map((stack) => {
        return (
          <Grid item>
            <Card className={classes.root}>
              <Grid container direction="row" justify="space-evenly" alignItems="center">
                {stack.name === 'Express' ? (
                  <></>
                ) : (
                  <Grid item>
                    <Avatar src={stack.icon} />
                  </Grid>
                )}
                <Grid item>
                  <Typography
                    variant="h5"
                    component="p"
                    style={
                      stack.name === 'Express' ? { fontFamily: 'Kanit', fontSize: 'xx-large' } : {}
                    }
                  >
                    {stack.name}
                  </Typography>
                </Grid>
              </Grid>
            </Card>
          </Grid>
        )
      })}
    </Grid>
  )
}
